. Hiervoor heb ik eerste een analyse geschreven. Waarna ik a.d.h. van deze analyse aan de slag kon met ontwikkeling van het portaal. Hieronder vindt u de documenten die ik in het kader van mijn stage heb gemaakt en dieper ingaan op bepaalde aspecten hiervan: ")])],1)],1),_c('v-row',[_c('v-col',[_c('v-card',[_c('v-card-title',{staticClass:"title"},[_vm._v("Plan van aanpak Mocked Sensor Portal")]),_c('v-card-actions',[_c('v-btn',{attrs:{"href":"/Plan_van_aanpak_Mocked_Sensor_Portal.pdf","target":"_blank"}},[_vm._v(" Bekijken ")])],1)],1)],1),_c('v-col',[_c('v-card',[_c('v-card-title',{staticClass:"title"},[_vm._v("Analyse Mocked Sensor Portal")]),_c('v-card-actions',[_c('v-btn',{attrs:{"href":"http://localhost:8080/analyse_Mocked_Sensor_Portal.pdf","target":"_blank"}},[_vm._v(" Bekijken ")])],1)],1)],1),_c('v-col',[_c('v-card',[_c('v-card-title',{staticClass:"title"},[_vm._v("Realisatie Mocked Sensor Portal")]),_c('v-card-actions',[_c('v-btn',{attrs:{"href":"http://localhost:8080/realisatie_Mocked_Sensor_Portal.pdf","target":"_blank"}},[_vm._v(" Bekijken ")])],1)],1)],1),_c('v-col',[_c('v-card',[_c('v-card-title',{staticClass:"title"},[_vm._v("Reflectie stage")]),_c('v-card-actions',[_c('v-btn',{attrs:{"href":"http://localhost:8080/reflectie_stage.pdf","target":"_blank"}},[_vm._v(" Bekijken ")])],1)],1)],1)],1)],1)}
